<template>
  <div class="container mt-3">
    <amount-pie :labels="labels"></amount-pie>
    <hr>
    <money-spent-bar :labels="labels"></money-spent-bar>
  </div>
</template>

<script>
import { api } from '../classes/Api';

export default {
  data: function() {
    return {
      labels: [],
    }
  },
  async mounted() {
    const stocksObject = await api.getAllStocks();

    const labels = {
      'food': {
        sum: 0,
        amount: 0,
      }, 
      'water': {
        sum: 0,
        amount: 0,
      }, 
      'medicine': {
        sum: 0,
        amount: 0,
      }, 
      'weapon': {
        sum: 0,
        amount: 0,
      }
    };

    for(const stockName in stocksObject) {
      const stockValue = stocksObject[stockName] ? stocksObject[stockName] : [];

      const statistics = stockValue.reduce((initialSum, value) => {

        labels[stockName]['amount'] += value.amount;
        labels[stockName]['sum'] += value.costPerPackage;

        return initialSum
      }, labels);


      labels[stockName] = {
        sum: statistics[stockName].sum.toFixed(2),
        amount: statistics[stockName].amount,
      };

      this.labels = labels;
    }
  },

  components: {
    MoneySpentBar: () => import ('../components/charts/MoneySpentBar'),
    AmountPie: () => import ('../components/charts/AmountPie'),
  },
}
</script>
